<template>
<div id="app">
  <div :class="$style.header">
    <img src="../../assets/m-play/logo.png" alt="">
    <div :class="$style.headerGo" @click="gotoDownload">
      <i class="iconfont icon-play2"></i>
      <span>????????????</span>
    </div>
  </div>
  <div :class="$style.playWrap" class="playWrap">
    <video :class="['video-js', 'vjs-melephant']" playsinline="playsinline"></video>
  </div>
  <div :class="$style.bodyGo" @click="gotoDownload">
    <img src="../../assets/m-play/logo2.png" alt="">
    <span>???????????????????????????????????????</span>
  </div>
  <div :class="$style.videoInfo">
    <label>{{videoInfos.name}}</label>
    <i class="iconfont icon-back"
      :class="videoSummaryShow ? '' : $style.upside"
      @click="videoSummaryShow = !videoSummaryShow"
    >
    </i>
    <p :class="$style.videoActor"><span>?????????</span>{{videoInfos.actor}}</p>
    <transition name="slide-fade">
      <p :class="$style.videoSummary" v-show="videoSummaryShow"><span>?????????</span>{{videoInfos.intro}}</p>
    </transition>
  </div>
  <div :class="$style.episodeArea" v-if="videoInfos.categories === 2">
    <label>??????</label>
    <span>{{videoInfos.extMap.updateEpisode}}</span>
    <div :class="$style.episodeWrap" ref="episodes">
      <ul>
        <li v-for="(episode, index) in episodes"
          :class="episode.episodeDisplay === currEpisode ? [$style.episodeActive, 'episode-active'] : ''"
          @click="episode.episodeDisplay === currEpisode ? '' : gotoEpisode(episode.videoDetailId)"
        >{{episode.episodeDisplay}}</li>
      </ul>
    </div>
  </div>
  <div :class="$style.recommend">
    <label>????????????</label>
    <ul>
      <li v-for="recommend in recommendVideo">
        <a :href="`/m-play.html?videoId=${recommend.videoBaseId}`" :class="$style.recommendLink">
          <span
            v-if="recommend.extMap.updateEpisode || recommend.extMap.clarity"
            :class="[$style.recommendSub]"
          >
          </span>
          <span
            v-if="recommend.extMap.updateEpisode"
            :class="[$style.recommendSubrb]"
          >
            {{ recommend.extMap.updateEpisode }}
          </span>
          <span
            v-if="recommend.extMap.clarity"
            :class="[$style.recommendSublb]"
          >
            {{ recommend.extMap.clarity }}
          </span>
          <img :src="adaptImgUrl(getImgUrl(recommend.images), 230, 337)" alt="">
        </a>
        <a :href="`/m-play.html?videoId=${recommend.videoBaseId}`">
          <p>{{recommend.name}}</p>
        </a>
      </li>
    </ul>
  </div>
</div>
</template>

<script>
const videojs = window.videojs
import commonMixins from '@/mixins/common'

export default {
  name: 'm-play',

  data () {
    return {
      vOptions: {
        // sources: [{
        //   src: 'http://om64jpv2l.bkt.clouddn.com/avengers-generic-1080',
        //   type: 'video/mp4',
        // }],
        sources: [],
        controls: true,
        width: document.body.offsetWidth,
        height: 425,
        poster: '',
      },
      videoSummaryShow: false,
      episodes: [],
      currEpisode: '',
      videoInfos: {},
      videoId: '',
      videoDetailId: '',
      recommendVideo: [],
      layout: 1,
      episodeIndex: 0,
      player: null,
    }
  },

  mixins: [commonMixins],

  created() {
    this.videoId = this.getUrlParam('videoId')
    this.videoDetailId = this.getHashValue() ? this.getHashValue() : ''

    this.fetchVideoData()
    this.fetchRecommendData()
  },

  beforeDestory () {
    this.dispose()
  },

  methods: {
    fetchWechatShare() {
      var oReq = new XMLHttpRequest()

      oReq.onload = (e) => {
        var data = oReq.response
        console.log(data, typeof data)
        if (data) {
          /* global wx:true */
          data = JSON.parse(data)
          wx.config({
            // debug: true,
            appId: 'wxc89ca66545c2df54',
            timestamp: data.timestamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
          })

          let shareImage = this.videoInfos.images.find(image => image.scale === 5)
          let shareImageUrl = ''
          if (!shareImage) {
            shareImageUrl = 'https://image.youxiang0210.com/wechat-share.png'
          } else {
            shareImageUrl = this.prefixImgUrl(shareImage.url, 400)
          }

          wx.ready(() => {
            wx.onMenuShareAppMessage({
              title: this.videoInfos.name,
              desc: this.videoInfos.intro.substring(0, 30),
              imgUrl: shareImageUrl
            })
            wx.onMenuShareTimeline({
              title: this.videoInfos.name,
              imgUrl: shareImageUrl
            })
          })
        }
      }
      oReq.open('POST', '/wechat/get_params')
      oReq.send()
    },
    getHashValue() {
      let hash = window.location.hash
      if (hash) {
        return hash.slice(1).replace(/&([^&]*)$/g, '')
      }
      return null
    },
    getVideoSources() {
      const videoBaseUrl = 'https://video.youxiang0210.com/'
      let sources = []

      let currentIndex = this.videoInfos.currentIndex
      sources = [{
        src: videoBaseUrl + this.videoInfos.vVideoDetailRspVos[currentIndex].videoResourceStatusVo.mp4Review.url,
        type: 'video/mp4',
      }]
      console.log(sources)
      return sources
    },
    prefixImgUrl(url, size, prefix = 'https://image.youxiang0210.com/') {
      if (DEBUG || BETA) {
        prefix = '//image.youxiang0210.com/'
      }
      if (size) {
        return prefix + url + '!' + size
      }
      return prefix + url
    },
    adaptImgUrl(url, width, height = 337, q = 100) {
      if (!width) {
        width = document.body.offsetWidth
      }
      let suffix = `?imageView2/2/w/${width}/h/${height}/q/${q}|imageslim`
      return url + suffix
    },
    getImgUrl(images) {
      let ret, arr
      switch (this.layout) {
        case 1:
          arr = images.filter(image => image.scale === 3)
          break
        case 2:
          arr = images.filter(image => image.scale === 1)
          break
        case 3:
          arr = images.filter(image => image.scale === 3)
          break
        default:
          break
      }
      ret = arr[0] ? this.prefixImgUrl(arr[0].url) : this.defaultImg
      return ret
    },
    gotoEpisode(id) {
      window.location.hash = id
      location.reload()
    },
    gotoDownload() {
      // let ifr = document.createElement('iframe')
      // ifr.src = 'com.daxiang.live://go?action=videoPlay&videoId=10000128&videoDetailId=800000123'
      // ifr.style.display = 'none'
      // document.body.appendChild(ifr)
      // location.href = 'https://fir.im/liveIOS'

      // setTimeout(() => {
      //   document.body.removeChild(ifr)
      // }, 2000)

      location.href = 'jcgroup://youxiang.com/go?action=videoPlay&videoId=' + this.videoId + '&videoDetailId=' + this.videoDetailId
      const now = Date.now()
      setTimeout(() => {
        if (Date.now() - now < 2550) {
          location.href = 'http://www.youxiang0210.com/m-index.html'
        }
      }, 2500)
    },
    async fetchVideoData() {
      let res = await this.$fetch('/m_web/videoBase/videoBaseMWebInfo', {
        videoBaseId: this.videoId,
        videoDetailId: this.videoDetailId,
      })
      if (res.code === 0) {
        this.videoInfos = res.data
        console.log(this.videoInfos)
        this.vOptions.sources = this.getVideoSources()
        this.vOptions.poster = this.prefixImgUrl(this.videoInfos.images[0].url)
        this.init()
        this.currEpisode = this.videoInfos.vVideoDetailRspVos[this.videoInfos.currentIndex].episodeDisplay
        this.episodes = this.videoInfos.vVideoDetailRspVos.map((item) => {
          return {
            episodeDisplay: item.episodeDisplay,
            videoDetailId: item.videoDetailId,
          }
        })
        if (this.videoInfos.categories === 2) {
          document.title = this.videoInfos.name + '-' + this.currEpisode
          this.$nextTick(() => {
            this.$refs.episodes.scrollLeft = document.querySelector('.episode-active').offsetLeft - 24
          })
        } else {
          document.title = this.videoInfos.name
        }
        this.fetchWechatShare()
      }
    },
    async fetchRecommendData() {
      let res = await this.$fetch('/web/channel/recommend', {
        videoBaseId: this.videoId,
        limit: 9,
      })
      if (res.code === 0) {
        this.recommendVideo = res.data
        console.log(res.data)
      }
    },
    dispose() {
      this.player.dispose()
      const videoEl = document.createElement('video')
      videoEl.setAttribute('class', 'video-js vjs-melephant')
      videoEl.setAttribute('playsinline', 'playsinline')
      document.querySelector('.playWrap').appendChild(videoEl)
    },
    buildErrorModal(player) {
      // let that = this
      let modal = videojs.getComponent('ClickableComponent')
      let theComponent = videojs.extend(modal, {
        name() {
          return 'ErrorDisplay'
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <p class="error-text">??????????????????</p>
                <p class="error-reload"><i class="iconfont icon-refresh"></i>????????????</p>
              </div>
            `,
            className: 'vjs-m-fullscreen vjs-melephant-error vjs-hidden'
          })
        },
        handleClick() {
          // console.log(that)
          // that.dispose()
          // that.init()
          player.play()
          // location.reload()
        }
      })
      videojs.registerComponent('ErrorDisplay', theComponent)
    },
    buildLoadingSpinner() {
      let loadingComponent = videojs.getComponent('LoadingSpinner')
      let Loading = videojs.extend(loadingComponent, {
        constructor: function(player, options) {
          loadingComponent.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <img class="loading-gif" src="${require('../../assets/m-play/loading.gif')}" alt="">
              </div>
            `,
            className: 'vjs-m-fullscreen vjs-melephant-loading vjs-hidden'
          })
        },
      })
      videojs.registerComponent('Loading', Loading)
    },
    buildModalComp() {
      let that = this
      let modal = videojs.getComponent('ClickableComponent')
      let theComponent = videojs.extend(modal, {
        name() {
          return 'EndDisplay'
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <i class="iconfont icon-download2"></i>
                <span class="end-text">??????????????????app</span>
              </div>
            `,
            className: 'vjs-m-fullscreen vjs-melephant-ended vjs-hidden'
          })
        },
        handleClick() {
          that.gotoDownload()
        }
      })
      videojs.registerComponent('EndDisplay', theComponent)
    },
    init() {
      let controlBar = {
        children: [
          'playToggle',
          'currentTimeDisplay',
          'progressControl',
          'durationDisplay',
          'fullscreenToggle',
        ]
      }

      const options = this.vOptions
      const theComponent = this
      let videoOption = {
        controls: options.controls, // ????????????
        autoplay: false, // ????????????
        width: options.width,
        height: options.height,
        poster: options.poster || '', // ??????
        techOrder: ['html5', 'flash'],
        sources: options.sources || [],
        controlBar,
        errorDisplay: false, // ????????????????????????
        loadingSpinner: false, // ????????????????????????
      }

      this.player = null
      this.player = videojs(document.querySelector('.video-js'), videoOption, function() {
        theComponent.buildLoadingSpinner()
        theComponent.buildErrorModal(this)
        theComponent.buildModalComp()

        this.addChild('Loading')
        this.addChild('ErrorDisplay')
        this.addChild('EndDisplay')

        this.on('error', () => {
          this.childNameIndex_.ErrorDisplay.show()
          this.childNameIndex_.Loading.hide()
          this.childNameIndex_.EndDisplay.hide()
        })

        this.on('seeking', () => {
          this.childNameIndex_.Loading.show()
        })
        this.on('waiting', () => {
          this.childNameIndex_.Loading.show()
        })

        this.on('play', () => {
          this.childNameIndex_.ErrorDisplay.hide()
          this.childNameIndex_.Loading.hide()
          this.childNameIndex_.EndDisplay.hide()
        })

        this.on('ended', () => {
          this.childNameIndex_.EndDisplay.show()
          this.controls(false)
        })

        this.on('fullscreenchange', (screen) => {
          if (!this.isFullscreen()) {
            this.childNameIndex_.ErrorDisplay.hide()
            this.childNameIndex_.Loading.hide()
            this.childNameIndex_.EndDisplay.hide()
            this.controls(true)
          }
        })

        // loading
        const loadingHideList = ['playing', 'canplay']
        for (let i = 0, j = loadingHideList.length; i < j; i++) {
          this.on(loadingHideList[i], () => {
            this.childNameIndex_.Loading.hide()
          })
        }
      })
    }
  },
}
</script>

<style lang="postcss" module>
:global(.slide-fade-enter-active) {
  transition: all .3s ease-in-out;
}
:global(.slide-fade-leave-active) {
  transition: all .3s ease-in-out;
}
:global(.slide-fade-enter), :global(.slide-fade-leave-active) {
  transform: translateY(-10px);
  opacity: 0;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 90px;
  background: rgba(0, 0, 0, 0.8);
  color: #FABE00;
  z-index: 100;
  img {
    float: left;
    margin: 17px 0 0 32px;
    height: 56px;
  }
  .headerGo {
    box-sizing: border-box;
    float: right;
    width: 173px;
    height: 50px;
    line-height: 42px;
    border: 2px solid #FABE00;
    margin: 19px 20px 0 0;
    border-radius: 25px;
    text-align: center;
    i {
      font-size: 28px;
      position: relative;
      top: 3px;
    }
    span {
      font-size: 22px;
    }
  }
}
.playWrap {
  overflow: hidden;
  height: 425px;
  margin-top: 90px;
  background: #000;
  user-select: none;
  box-shadow: 0 -200px 150px 100px #000;
}
.bodyGo {
  box-sizing: border-box;
  width: 93.33%;
  height: 80px;
  line-height: 70px;
  margin: 16px auto 21px;
  border: 2px solid #D0D0D0;
  border-radius: 10px;
  text-align: center;
  img {
    display: inline-block;
    position: relative;
    top: 15px;
    width: 50px;
  }
  span {
    color: #4a4a4a;
  }
}
.videoInfo {
  overflow: hidden;
  box-sizing: border-box;
  min-height: 118px;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
  padding: 24px;
  label {
    color: #4a4a4a;
  }
  i {
    float: right;
    display: inline-block;
    color: #9b9b9b;
    font-size: 32px;
    transition: 0.2s;
    &.upside {
      transform: rotate(180deg);
    }
  }
  p {
    color: #9b9b9b;
    font-size: 26px;
    line-height: 36px;
    span {
      color: #4a4a4a;
    }
    &.videoActor {
      margin-top: 6px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    &.videoSummary {
      margin-top: 12px;
    }
  }
}
.episodeArea {
  overflow: hidden;
  box-sizing: border-box;
  min-height: 164px;
  border-bottom: 1px solid #efefef;
  padding: 24px;
  color: #4a4a4a;
  span {
    float: right;
    font-size: 26px;
    color: #9b9b9b;
  }
  .episodeWrap {
    overflow: scroll;
    width: 100%;
    ul {
      min-width: 100%;
      white-space: nowrap;
      li {
        display: inline-block;
        background: #f2f2f2;
        margin-top: 24px;
        padding: 20px 16px;
        margin-right: 10px;
        border-radius: 8px;
        font-size: 24px;
        &.episodeActive {
          background: #FFC31B;
          color: #fff;
        }
      }
    }
  }
}
.recommend {
  overflow: hidden;
  box-sizing: border-box;
  min-height: 172px;
  padding: 24px;
  color: #4a4a4a;
  ul {
    li {
      width: 230px;
      height: 388px;
      display: inline-block;
      text-align: center;
      margin: 12px 6px 0 0;
      &:nth-child(3n) {
        margin-right: 0;
      }
      .recommendLink {
        display: block;
        position: relative;
        width: 230px;
        height: 337px;
        img {
          width: 100%;
          height: 337px;
        }
      }
      p {
        font-size: 24px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: 12px 0;
      }
    }
  }
  .recommendSub {
    position: absolute;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), transparent);
    left: 0;
    bottom: 0;
    width: 100%;
    height: 28px;
    &rb {
      position: absolute;
      color: #fff;
      font-size: 24px;
      right: 0;
      bottom: 0;
    }
    &lb {
      position: absolute;
      color: #fff;
      font-size: 24px;
      left: 0;
      bottom: 0;
    }
  }
}
</style>



